<template>
  <div>
    <!---->
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>用户管理
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon>用户设置
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
      <span class="screenContent">
        <img
          style="width: 10.5px;margin-right: 8px"
          src="../assets/screen.png"
        >
        <a
          href="javascript:void(0)"
          @click="isShowScreen"
        >
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
    <Content
      v-if="isScreen"
      class="content isScreen"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="62"
      >
        <Row>
          <Col
            style="margin-bottom: 15px;"
            :xs="{ span: 5 }"
            :lg="{ span: 22}"
          >
          <FormItem label="单位名称">
            <Input
              v-model="searchData.orgName"
              placeholder="请填写单位名称"
            ></Input>
          </FormItem>
          </Col>
        </Row>
        <Row>
          <Col
            :xs="{ span: 7 }"
            :lg="{ span: 7}"
          >
          <FormItem label="用户名">
            <Input
              v-model="searchData.username"
              placeholder="请填写用户名"
            ></Input>
          </FormItem>
          </Col>
          <Col
            class="userScreen"
            :xs="{ span: 7}"
            :lg="{ span: 7}"
          >
          <FormItem label="身份">
            <Select
              v-if="!isDisabled"
              v-model="searchData.identityId"
              clearable
              filterable
              placeholder="请选择身份"
              :transfer="transferValue"
            >
              <Option value="1">一般用户</Option>
              <Option value="2">查询用户</Option>
              <Option value="3">审核用户</Option>
              <Option value="4">超级用户</Option>
            </Select>
            <Input
              v-else
              v-model.trim="searchData.orgName"
              disabled
              placeholder="请填写身份"
            ></Input>
          </FormItem>
          </Col>
          <Col
            class="userScreen"
            :xs="{ span: 7}"
            :lg="{ span: 7 }"
          >
          <FormItem label="权限组">
            <Select
              v-model="searchData.roleId"
              clearable
              filterable
              placeholder="请选择权限组"
            >
              <Option
                v-for="item in permissionList"
                :key="item.adminRoleId"
                :value="item.adminRoleId"
              >{{ item.adminRoleName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            style="float:right"
            :xs="{ span: 1}"
            :lg="{ span: 1, offset: 1 }"
          >
          <Button type="primary" @click="getUserData">
            确认
          </Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="createData"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="modifyData"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="deleteData"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(6)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/user/exportUserListExcel')">
        导出
      </Button>
      <Table
        ref="checkTable"
        highlight-row
        class="table"
        :columns="columns"
        :data="data"
        @on-current-change="selectRow"
      ></Table>
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!--详情弹出框-->
      <Modal
        v-model="selTypeMod"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled && isCreate">请设置用户信息</span>
          <span v-else>用户信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="form"
              :model="form"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Row>
                <Col span="12">
                <FormItem
                  label="用户名"
                  prop="username"
                >
                  <Input
                    v-model.trim="form.username"
                    :disabled="isDisabled"
                    placeholder="请填写用户名"
                  ></Input>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="身份"
                  prop="identity"
                >
                  <Select
                    v-model="form.identity"
                    :disabled="isDisabled"
                    clearable
                    filterable
                    placeholder="请选择身份"
                  >
                    <Option v-for="(item,key) in identity" :key="key" :value="parseInt(key)">{{ item }}</Option>
                  </Select>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem label="密码" prop="password">
                  <Input
                    v-model.trim="form.password"
                    type="password"
                    :disabled="isDisabled"
                    placeholder="请填写密码"
                  ></Input>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem label="确认密码" prop="passwordConfirm">
                  <Input
                    v-model.trim="form.passwordConfirm"
                    type="password"
                    :disabled="isDisabled"
                    placeholder="请填写确认密码"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem
                  label="权限组"
                  prop="roleId"
                >
                  <Select
                    v-model="form.roleId"
                    :disabled="isDisabled"
                    :transfer="transferValue"
                    filterable
                    clearable
                    placeholder="请选择权限组"
                  >
                    <Option
                      v-for="item in permissionList"
                      :key="item.adminRoleId"
                      :value="item.adminRoleId"
                    >{{ item.adminRoleName }}</Option>
                  </Select>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="使用人"
                  prop="linkManId"
                >
                  <Select
                    v-model="form.linkManId"
                    :disabled="isDisabled"
                    :transfer="transferValue"
                    clearable
                    filterable
                    placeholder="请选择使用人"
                  >
                    <Option
                      v-for="item in linkManList"
                      :key="item.linkManId"
                      :value="item.linkManId"
                    >{{ item.linkManName }}</Option>
                  </Select>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem
                  label="使用单位"
                  prop="danweiIdUse"
                >
                  <Select
                    v-model="form.danweiIdUse"
                    :disabled="isDisabled"
                    clearable
                    :transfer="transferValue"
                    filterable
                    placeholder="请选择使用单位"
                    @on-change="getOfficeData(true,1)"
                  >
                    <Option
                      v-for="item in orgList"
                      :key="item.orgId"
                      :value="item.orgId"
                    >{{ item.orgName }}</Option>
                  </Select>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="使用部门"
                  prop="orgIdUse"
                >
                  <Select
                    v-model="form.orgIdUse"
                    :disabled="isDisabled"
                    :transfer="transferValue"
                    clearable
                    filterable
                    placeholder="请选择使用部门"
                    @on-change="getLinkManData"
                  >
                    <Option
                      v-for="item in officeList"
                      :key="item.officeId"
                      :value="item.officeId"
                    >{{ item.officeName }}</Option>
                  </Select>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem
                  label="主管单位"
                  prop="manaDanweiId"
                >
                  <Select
                    v-model="form.manaDanweiId"
                    :transfer="transferValue"
                    :disabled="isDisabled"
                    clearable
                    filterable
                    placeholder="请选择主管单位"
                    @on-change="getOfficeData(true,0)"
                  >
                    <Option
                      v-for="item in orgList"
                      :key="item.orgId"
                      :value="item.orgId"
                    >{{ item.orgName }}</Option>
                  </Select>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="主管部门"
                  prop="manaOrgId"
                >
                  <Select
                    v-model="form.manaOrgId"
                    :disabled="isDisabled"
                    :transfer="transferValue"
                    clearable
                    filterable
                    placeholder="请选择主管部门"
                  >
                    <Option
                      v-for="item in manaOfficeList"
                      :key="item.officeId"
                      :value="item.officeId"
                    >{{ item.officeName }}</Option>
                  </Select>
                </FormItem>
                </Col>
              </Row>
              <Col span="24">
              <FormItem
                label="资金类别"
                prop="capitalId"
              >
                <Select
                  v-model="form.capitalId"
                  :disabled="isDisabled"
                  :transfer="transferValue"
                  clearable
                  filterable
                  placeholder="请选择资金类别"
                >
                  <Option
                    v-for="item in capitalList"
                    :key="item.capitalId"
                    :value="item.capitalId"
                  >{{ item.capitalName }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="24">
              <FormItem label="备注">
                <Input
                  v-model.trim="form.userRemark"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写备注"
                ></Input>
              </FormItem>
              </Col>
            </Form>
          </TabPane>
          <TabPane
            class="uploadStyle"
            label="附件"
            name="name2"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="form" />
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">
            打印
          </Button>
          <Button icon="md-exit">
            导出
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="resetType('form')"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="submitType('form')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!--/详情弹出框-->
    </Content>
  </div>
</template>

<script>
import { dateformat } from '@/filters/index.js'
import uploadFile from '@/components/fileUpload.vue'
import excelExcel from '@/mixins/exportExcel.js'
export default {
  name: 'UserManage',
  components: {
    uploadFile
  },
  mixins: [excelExcel],
  data() {
    const validatePassCheck = (rule, value, callback) => {
      const number = /[0-9]/;
      const capital = /[a-zA-Z]/;
      const character = /[#?!@$%^&*-]/;
      if (value === '') {
        callback(new Error('请输入密码'));
      } else if (!number.exec(value)) {
        callback(new Error('请输入数字'));
      } else if (!capital.exec(value)) {
        callback(new Error('请输入字母'));
      } else if (!character.exec(value)) {
        callback(new Error('请输入特殊字符'));
      } else if (value.length < 6) {
        callback(new Error('密码长度不能小于6位'));
      } else {
        if (this.form.passwordConfirm === this.form.password) {
          this.$refs.form.validateField('passwordConfirm')
        }
        callback();
      }
    };
    const validatePassConfirmCheck = (rule, value, callback) => {
      const password = this.form.passwordConfirm
      if (password === '') {
        callback(new Error('请输入确认密码'));
      } else if (password !== this.form.password) {
        callback(new Error('密码与确认密码不一致'));
      } else {
        callback();
      }
    };
    // 自定义序号
    const indexMethod = (row) => {
      const index = row._index
      return this.searchData.pageSize * (this.searchData.pageNo - 1) + index + 1;
    }
    return {
      // 添加|编辑
      isCreate: false,
      // 权限下拉
      permissionList: [],
      officeList: [],
      manaOfficeList: [],
      capitalList: [],
      // 联系人下拉
      linkManList: [],
      // 主管单位/使用单位 下拉框
      orgList: [],
      total: 0,
      isDisabled: false,
      transferValue: true,
      form: {
        // 用户名
        username: '',
        // 身份
        identity: '',
        // 密码
        password: '',
        passwordConfirm: '',
        // 使用单位
        danweiIdUse: '',
        // 使用人
        linkManId: '',
        // 权限组
        roleId: '',
        // 使用部门
        orgIdUse: '',
        // 主管单位
        manaDanweiId: '',
        // 主管部门
        manaOrgId: '',
        // 资金类别
        capitalId: '',
        // 备注
        userRemark: ''
      },
      ruleValidateType: {
        manaOrgId: [
          { type: 'number', required: true, message: '请选择主管部门', trigger: 'blur' }
        ],
        manaDanweiId: [
          { type: 'number', required: true, message: '请选择主管单位', trigger: 'change' }
        ],
        capitalId: [
          { type: 'number', required: true, message: '请选择资金类别', trigger: 'change' }
        ],
        roleId: [
          { type: 'number', required: true, message: '请选择权限组', trigger: 'change' }
        ],
        linkManId: [
          { type: 'number', required: true, message: '请选择使用人', trigger: 'blur' }
        ],
        danweiIdUse: [
          { type: 'number', required: true, message: '请选择使用单位', trigger: 'change' }
        ],
        username: [
          { required: true, message: '请填写用户名', trigger: 'blur' }
        ],
        identity: [
          { type: 'number', required: true, message: '请选择身份', trigger: 'change' }
        ],
        orgIdUse: [
          { type: 'number', required: false, message: '请选择使用部门', trigger: 'change' }
        ],
        password: [
          { required: true, validator: validatePassCheck, trigger: 'change' }
        ],
        passwordConfirm: [
          { required: true, validator: validatePassConfirmCheck, trigger: 'change' }
        ]
      },
      // 筛选内容
      searchData: {
        orgName: '',
        roleId: '',
        identityId: '',
        username: '',
        ascending: false,
        orderBy: 'userId',
        pageNo: 1,
        pageSize: 10
      },
      isScreen: false,
      curTime: '',
      showContact: false,
      selTypeMod: false,
      checkText: '提交审核',
      identity: {
        1: '一般用户',
        2: '查询用户',
        3: '审核用户',
        4: '超级用户'
      },
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          indexMethod: indexMethod
        },
        {
          title: '用户名',
          width: 180,
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row)
                }
              }
            }, params.row.username);
          }
        },
        {
          title: '身份',
          width: 90,
          render: (h, params) => {
            const info = params.row.identity
            const identity = info ? this.identity[info] : ''
            return h('div', identity);
          }
        },
        {
          title: '权限组',
          key: 'roleName'
        },
        {
          title: '单位名称',
          key: 'orgName'
        },
        {
          title: '使用人',
          key: 'linkManName',
          width: 90
        },
        {
          title: '创建时间',
          width: 150,
          key: 'createTime',
          render: (h, params) => {
            return h('div',
              dateformat(params.row.createTime, 'YYYY-MM-DD HH:mm')
            )
          }
        }
      ],
      data: [
      ],
      currentRow: {}
    }
  },
  mounted() {
    // 表格
    this.getUserData()
    // 部门(主管单位)下拉框
    this.getOfficeData()
    // 资金类别
    this.getFundData()
    // 权限下拉
    // TODO: 权限设置.. 还没有相对应的api
    this.getPowerData()
    this.getOrganize()
    this.getContactData()
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    pageNoChange(pageNo) {
      this.searchData.pageNo = pageNo
      this.getUserData()
    },
    pageSizeChange(pageSize) {
      this.searchData.pageSize = pageSize
      this.getUserData()
    },
    getUserData() {
      const that = this;
      that.$set(that, 'data', []) // 需要做这个操作，以防空数据留至其他行
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/user/selectUser/list', that.searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          that.data = data.userModelList
          that.total = data.totalCount
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
      that.currentRow = {}
    },
    clearData() {
      this.form = {
        identity: '',
        password: '',
        passwordConfirm: '',
        danweiIdUse: '',
        linkManId: '',
        roleId: '',
        orgIdUse: '',
        manaDanweiId: '',
        manaOrgId: '',
        capitalId: '',
        userRemark: '',
        fileIds: null
      }
      this.$refs['form'].resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    // 打开详情弹出来
    showInfo(row) {
      const data = Object.assign({}, row)
      this.clearData();
      this.isDisabled = true;
      this.form = data;
      this.form.passwordConfirm = data.password;
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
      })
      this.selTypeMod = true
    },
    // 选中一行进行修改数据
    selectRow(currentRow) {
      this.isSelected = true;
      this.currentRow = currentRow;
    },
    // 修改数据
    modifyData() {
      const that = this;
      const row = that.currentRow;
      that.clearData();
      that.isCreate = false;
      that.isDisabled = false;
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要修改的内容');
        return
      }
      that.form = Object.assign({}, row);
      that.form.passwordConfirm = row.password;
      // 使用人、使用部门、主管部门的下拉框数据更新
      // 使用部门
      that.getOfficeData(true, 1, () => {
        // 主管部门
        that.getOfficeData(true, 0, () => {
          that.form.linkManId = row.linkManId
          that.form.orgIdUse = row.orgIdUse
          that.form.manaOrgId = row.manaOrgId
          that.form.fileids = row.fileids
          that.$nextTick(() => {
            that.$refs.uploadFile.getFileItems()
          })
          that.$forceUpdate();
          that.selTypeMod = true;
        })
      })
    },
    // 删除用户
    deleteData() {
      const that = this;
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要删除的内容');
        return
      }
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/user/deleteUser/' + that.currentRow.userId, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.isSelected = false;
              that.$Message.success(res.msg);
              that.getUserData()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            this.$Message.error('网络连接错误');
            this.$Spin.hide();
          }, that)
        }
      });
    },
    createData() {
      // 添加
      this.isCreate = true;
      this.isDisabled = false;
      this.clearData();
      this.$forceUpdate();
      this.selTypeMod = true;
    },
    // 权限下拉框
    getPowerData() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/permission/findPermissionList', {
        adminRoleName: ''
      }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.permissionList = res.data.permissionList;
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    submitType(name) {
      const that = this;
      const form = that.form
      form.fileIds = that.$refs.uploadFile.getFileIds()
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          // 添加
          if (that.isCreate) {
            that.commonUtil.ajaxPostUtil('/user/addUser', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.selTypeMod = false;
                that.$Message.success(res.msg);
                that.getUserData()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          } else {
            that.commonUtil.ajaxPutUtil('/user/updateUser', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.selTypeMod = false;
                that.$Message.success(res.msg);
                that.getUserData()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
          that.currentRow = {}
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    resetType(name) {
      this.selTypeMod = false;
    },
    // 筛选按钮
    isShowScreen() {
      this.isScreen = !this.isScreen;
    },
    // 获取主管单位
    getOrganize() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/organization/searchOrganization/list', {
        orgType: '',
        orgName: '',
        ascending: false,
        orderBy: 'orgId'
      }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.orgList = res.data.orgList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    getLinkManData(officeId) {
      this.getContactData(true, this.form.danweiIdUse, officeId)
    },
    // 使用人下拉框
    getContactData(flag, orgId, officeId, callback) {
      const that = this
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/linkMan/searchLinkMan/list', {
        orgId: flag ? orgId : null,
        officeId: flag ? officeId : null,
        linkManName: '',
        ascending: false,
        orderBy: 'linkManId'
      }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.linkManList = res.data.linkManList
          if (callback) callback(true)
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        if (callback) callback(false)
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 部门
    getOfficeData(flag, param, callback) {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      let ordId = null
      if (flag) {
        const form = this.form
        ordId = (param === 1 ? form.danweiIdUse : form.manaDanweiId)
      }
      that.commonUtil.ajaxPostUtil('/office/searchOffice/list', { orgId: ordId, officeName: '' }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const list = res.data.officeList
          if (flag) {
            if (param === 1) {
              if (!callback) {
                that.form.linkManId = null
                that.form.orgIdUse = null
              }
              that.officeList = list
              that.getContactData(true, ordId, that.form.orgIdUse, callback)
            } else {
              that.manaOfficeList = list
              if (callback) callback(true)
              else {
                that.form.manaOrgId = null
              }
            }
          } else {
            that.officeList = list
            that.manaOfficeList = list
          }
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        if (callback) callback(false)
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    getFundData() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/capital/searchCapital/list', {
        capitalName: '',
        ascending: false,
        orderBy: 'capitalId'
      }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.capitalList = res.data.capitalList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    }
  }
}
</script>

<style>
.ivu-drawer-body > div {
  margin: 20px 0;
}
.ivu-drawer-body > div b {
  margin-left: 3px;
}
/*<!--选择弹出框的样式-->*/
.selStyle .ivu-radio-wrapper {
  font-weight: bold;
}
.selStyle .ivu-modal-body {
  line-height: 2rem;
}
.selStyle .ivu-radio-wrapper {
  margin-right: 21px;
}
.ivu-page-options-sizer {
  margin-right: 0;
}
.breadcrumb i {
  margin-right: 8px;
}
.ivu-breadcrumb {
  font-size: 12px;
  margin: 20px 0;
}
.ivu-menu-submenu-title {
  font-weight: bold;
}
.ivu-menu-submenu-title span > i,
.ivu-menu-submenu-title > i {
  margin-right: 4px;
}
.ivu-menu-vertical .ivu-menu-item,
.ivu-menu-vertical .ivu-menu-submenu-title {
  font-size: 12px;
  color: #495060;
}
.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
  font-weight: bold;
}
.ivu-menu-submenu.ivu-menu-opened {
  padding-top: 8px;
}
.userScreen .ivu-form-item-label {
  width: 51px !important;
}
.userScreen .ivu-form-item-content {
  margin-left: 51px !important;
}
.userScreen {
  margin-left: 2.07%;
}
</style>




